height: 1.42857;\r\n        padding: 10px;\r\n        vertical-align: middle;\r\n    }\r\n    .ibx-hotTop-rank {\r\n        float: left;\r\n        width: 16px;\r\n        height: 16px;\r\n        line-height: 16px;\r\n        color: #fff;\r\n        text-align: center;\r\n        margin: 10px 10px;\r\n        margin-left: 0;\r\n    }\r\n    tbody td .first {\r\n        background-color: #f44528;\r\n    }\r\n    tbody td .bottom{\r\n        background-color: #ff943e;\r\n    }\r\n    a :hover{\r\n        cursor: pointer;\r\n    }\r\n</style>\r\n<script>\r\n    import   analysis from \"../../ComonJs/formatAnalysis\"\r\n    import   getIcon from  \"../../ComonJs/formatIcon\"\r\n    export default {\r\n        props: {\r\n            id:{\r\n                type:String,\r\n                default:\"\"\r\n            },\r\n            data: Object,\r\n            title: String,\r\n            class:{\r\n                type:String,\r\n                default:\"\"\r\n            },\r\n            style:{\r\n                type:String,\r\n                default:\"\"\r\n            },\r\n            hidden:{\r\n                type:Boolean,\r\n                default:false\r\n            },\r\n            ischange:Number,\r\n            ontdclick:Function,\r\n            onrefresh:Function\r\n        },\r\n        data:function () {\r\n            return{\r\n                urlContainer:[],    //放置所有格式化之后的url\r\n                refresh:\"refresh\",\r\n                topdata:''\r\n            }\r\n        },\r\n       mounted:function () {\r\n           //如何只有一行数据可以跳转的时候\r\n           //this.setData();\r\n       },\r\n        methods: {\r\n            setData:function () {\r\n                this.topdata=this.data;\r\n                if(this.data&&this.data!=null) {\r\n                    if(this.data.fields&&this.data.fields!=[]){\r\n                        var field=this.data.fields;\r\n                        for(var i=0;i<field.length;i++){\r\n                        