          size: {\n                type: Number,\n                default: 10\n            },\n            // 数据总大小\n            count: {\n                type: Number,\n                default: 1\n            },\n            // 显示分页标签个数\n            pagenum: {\n                type: Number,\n                default: 10\n            },\n            //点击分页返回 当前页码\n            onclick: Function,\n\n            //分页组件大小    lg大尺寸，   sm小尺寸\n            pagersize: {\n                type: String,\n                default: \"\"\n            }\n        },\n        data: function() {\n            let islast = this.count > this.size;\n            var isLg = this.pagersize == \"lg\" ? true : false;\n            var isSm = this.pagersize == \"sm\" ? true : false;\n            return {\n                activeNum: 1,\n                pages: [],\n                firstShow: false,\n                lastShow: islast,\n                maxPage: Math.ceil(this.count / this.size),\n                classObj: {\n                    'pagination': true,\n                    'boot-page': true,\n                    'pagination-lg': isLg,\n                    'pagination-sm': isSm\n                }\n            }\n        },\n\n        methods: {\n            // 点击页码刷新数据\n            onPageClick: function(page) {\n                this.activeNum = page;\n            },\n\n            // 上一页\n            onPrevClick: function() {\n                // 当前页是否为当前最小页码\n                if (this.activeNum > 1) {\n                    //this.activeNum = this.activeNum - 1\n                    if (this.activeNum == this.pages[0]) {\n                        this.pages = [];\n                        for (var i = this.activeNum - this.pagenum; i < this.activeNum; i++) {\n                            this.pages.push(i);\n                        }\n                    }\n                    this.activeNum = this.activeNum - 1;\n                }\n            },\n\n            // 下一页\n            onNextClick: function() {\n                // 当前页是否为当前最大页码\n                if (this.activeNum < this.maxPage) {\n                    if (this.activeNum == this.pages[this.pages.length - 1]) {\n                        this.pages = [];\n                        for (var i = this.activeNum + 1; i < this.activeNum + 1 + this.pagenum; i++) {\n                            if (i <= this.maxPage)\n                                this.pages.push(i);\n                        }\n                    }\n                    this.activeNum = this.activeNum + 1;\n                }\n            },\n\n            // 第一页\n            onFirstClick: function() {\n                if (this.pages[0] === 1) {\n                    this.activeNum = 1;\n                } else {\n                    this.pages = [];\n\n                    for (var i = 1; i <= this.pagenum; i++) {\n                        this.pages.push(i);\n                    }\n                    this.activeNum = 1;\n                }\n            },\n\n            // 最后一页\n            onLastClick: function() {\n                if (this.pages[this.pages.length - 1] == this.maxPage) {\n                    this.activeNum = this.maxPage;\n                } else {\n                    var lastPage = [];\n                    var num = this.maxPage % this.pagenum;\n                    if (num == 0) num = this.pagenum;\n                    for (num; num > 0; num--) {\n                        lastPage.push(this.maxPage - num + 1);\n                    }\n                    this.pages = lastPage;\n                    this.activeNum = this.maxPage;\n                }\n            },\n\n            // 获取页码\n            getPages: function() {\n                this.maxPage = Math.ceil(this.count / this.size);\n                this.lastShow = this.count > this.size;\n                this.pages = [];\n                // 比较总页码和显示页数\n                if (this.maxPage <= this.pagenum) {\n                    for (var i = 1; i <= this.maxPage; i++) {\n                        this.pages.push(i);\n                    }\n                } else {\n                    for (var i = 1; i <= this.pagenum; i++) {\n                        this.pages.push(i);\n                    }\n                }\n            },\n\n            // 页码变化获取数据\n            getData: function() {\n                this.onclick && this.onclick(this.activeNum);\n            }\n        },\n        mounted: function() {\n            this.getPages();\n        },\n\n        watch: {\n            // 监听显示数量\n            'size': function() {\n                this.getPages();\n                if (this.activeNum + 1 > this.pages.length) {\n                    this.activeNum = this.pages.length - 1;\n                }\n                this.getData();\n            },\n\n            'count': function() {\n                this.getPages();\n            },\n\n            // 监测当前页变化\n            'activeNum': function() {\n                if (this.activeNum == 1)\n                    this.firstShow = false;\n                else\n                    this.firstShow = true;\n\n                if (this.activeNum == this.maxPage)\n                    this.lastShow = false;\n                else\n                    this.lastShow = true;\n                this.getData();\n            }\n        }\n    }\n</script>\n\n<style scoped>\n    .boot-select {\n        float: right;\n        width: 80px;\n    }\n    \n    .boot-nav {\n        float: left;\n    }\n    \n    .boot-page {\n        display: inline-block;\n        vertical-align: middle;\n    }\n    \n    .page-total {\n        display: inline-block;\n        vertical-align: middle;\n    }\n</style>"],"sourceRoot":"webpack://"}]);

