<template>
    <div id=".searchList">
        <searchlist :filters="filterlist" :data="datalist" :highlight="highlight" :showfilter="showfilter" :allowpaging="allowpaging"
            :size="size" :count="count" :onselectpage="Pager" :onitemclick="TitleClick" :oncollect="collectClick" :onfilterclear="FilterClean"
            :onfilterclick="FilterClick">
        </searchlist>
    </div>
</template>
<script>
    import soUI from '../../SoUI/soUI';
    var listModel = {
        id: "",
        title: "",
        source: "",
        author: "",
        createDate: "",
        summary: "",
        imgStr: "",
        format: ""
    };

    var filterdata = {
        "title": "",
        "filter": "",
        "checkbox": false,
        "droplist": []
    };

    var ftitleModel = {
        "s:datasourcename": "来源",
        "s:format": "格式",
        "format": "文件类型",
        "ep:o:organization": "单位",
        "b:contributor": "作者"
    };
    export default {
        props: {
            param: Object
        },
        data: function data() {
            return {
                allowpaging: false,
                showfilter: false,
                titletemplate: "",
                filterlist: [],
                filtertitle: "筛选条件",
                tip: "",
                pagenum: 10,
                size: 10,
                count: 1,
                strTemplate: "",
                datalist: [],
                highlight: [],
                collections: [],
            }
        },
        mounted: function () {
            var pagefilter = { "from": 0, "size": this.size };
            this.SetParms(pagefilter);
            this.getData();
        },
        methods: {
            //石亚茹Match接口
            getData: function () {
                let pmdata = this.param;
                delete pmdata["viewtype"];
                let _this = this;
                debugger;
                $.ajax({
                    url: "/Document/GetMatchData",
                    data: { param: JSON.stringify(pmdata) },
                    type: "get",
                    cache: false,
                    async: false,
                    dataType: 'json',
                    success: function (result) {
                        let data = JSON.parse(result);
                        _this.count = data.count;
                        _this.GetCollection();
                        _this.GetListData(data.metadatas);
                        if (data.count > _this.size) _this.allowpaging = true;
                    },
                    error: function (err) {
                        console.log(err);
                    }
                });
            },

            Pager: function (pageIndex) {
                var startIndex = (pageIndex - 1) * this.size;
                var pagefilter = { "from": startIndex, "size": this.size };
                this.SetParms(pagefilter);
                this.getData();
            },

            TitleClick: function (value) {
                window.open("/Render/PTDetail?iiid=" + value.id)
            },

            BindData: function (data) {
                this.count = data.count;
                //this.GetListData(data.metadatas);
                //this.GetFilterData(data.groups);
            },

            collectClick: function (item) {
                let data = {
                    "BehaviorType": "favorite",
                    "Type": "list",
                    "Title": item.title,
                    "Content": JSON.stringify({ id: item.id }),
                };
                if (item.collectImg == "save") {
                    $.ajax({
                        url: "/UserBehavior/AddFavoriteMess",
                        data: { data: JSON.stringify(data) },
                        type: "get",
                        async: false,
                        cache: false,
                        dataType: 'json',
                        success: function (result) {
                            gtUI.message({ message: "收藏成功！", type: 'success' });
                        },
                        error: function (result) {
                            gtUI.message({ message: "收藏失败！", type: 'warning' });
                            console.log(result);
                        }
                    });
                }
                else gtUI.message({ message: "已收藏！", type: 'info' });
            },

            FilterClick: function () { },
            FilterClean: function () { },

            GetCollection() {
                let _this = this;
                $.ajax({
                    url: "/UserBehavior/GetAllFavoriteMessByUserId",
                    data: {},
                    type: "get",
                    cache: false,
                    async: false,
                    dataType: 'json',
                    success: function (result) {
                        _this.collections = result;
                    },
                    error: function (result) {
                        console.log(result);
                    }
                });
            },

            GetCollectImg: function (id) {
                for (let i in this.collections) {
                    let content = { id: "" };
                    try {
                        content = JSON.parse(this.collections[i]["Content"]);
                        if (content.id == id) return "collect";
                    }
                    catch (err) {
                    }
                }
                return "save";
            },

            GetListData: function (data) {
                this.datalist = [];
                for (var i = 0; i < data.length; i++) {
                    listModel = {
                        id: "",
                        source: "",
                        title: "",
                        collectImg: "",
                        format: "",
                        createDate: "",
                        author: "",
                        summary: "",
                        imgStr: "",
                    };
                    var item = data[i];
                    listModel.id = item.iiid || "";
                    listModel.source = item["source"]["name"] || "";

                    //title有多个
                    listModel.title = item["dc"]["title"][0]["text"] || "";
                    listModel.collectImg = this.GetCollectImg(listModel.id);
                    //format找不到对应字段
                    listModel.format = item["source"]["format"] || "";
                    if (listModel.format != "")
                        listModel.format = listModel.format.toLocaleLowerCase();

                    let dateStr = item["indexeddate"] || item["dc"]["date"][0]["value"] || ""
                    listModel.createDate = dateStr.substr(0, dateStr.indexOf('T'));

                    let authors = item["dc"]["contributor"] || [];
                    for (let j = 0; j < authors.length; j++) {
                        if (authors[j]["type"] == "Author") {
                            listModel.author = authors[j]["name"] || "";
                            break;
                        }
                    }

                    let descrips = item["dc"]["description"] || [];
                    for (let j = 0; j < descrips.length; j++) {
                        if (descrips[j]["type"] == "Summary") {
                            listModel.summary = descrips[j]["text"] || "";
                            break;
                        }
                    }

                    listModel.imgStr = item["thumbnail"] || "";
                    if (listModel.imgStr != "" && listModel.imgStr.indexOf('data:image') != 0)
                        listModel.imgStr = "data:image/gif;base64," + listModel.imgStr;
                    this.datalist.push(listModel);
                }
            },

            GetFilterData: function (data) {
                this.filterlist = [];
                this.filterlist.push(filterdata);
                for (var title in ftitleModel) {
                    if (data[title]) {
                        let filterModel = [];
                        filterModel.droplist = [];
                        filterModel.checkbox = true;
                        filterModel.filter = title;
                        filterModel.title = ftitleModel[title];
                        for (var name in data[title]) {
                            filterModel.droplist.push({ text: name, value: "", count: "" });
                        }
                        this.filterlist.push(filterModel);
                    }
                }
            },
            SetParms: function (pagefilter) {
                this.param.pager = pagefilter;
            }
        },
        components: {
            searchlist: soUI.searchlist
        }
    };
</script>

<style>
    .searchList {
        font-family: '微软雅黑', arial, verdana, sans-serif;
        font-size: 14px;
    }
</style>


