<template>
    <div class="newEstPt">
        <top  :data="data" :title="title"
              :style="style" :hidden="hidden" :class="" :ischange="ischange"
              :onRefresh="refresh" :ontdclick="tdClick">
        </top>
    </div>
</template>
<script>
    import soUI from '../../SoUI/soUI';
    //import ajax from '../../Utils/pollAjax';
    //import _ajax from '../../Utils/coment'
    var fields = [{
        "name": "name",
        "title": "名称",
        "type": "",
        "key": false,
        "visible": "",
        "url": "/Render/PTDetail?iiid=<#= id#>"
    }, {
        "name": "author",
        "title": "作者",
        "type": "",
        "key": false,
        "visible": "",
    }, {
        "name": "downloadcount",
        "title": "下载次数",
        "type": "",
        "key": false,
        "visible": ""
    }];
    var count = 1;
    var title = "最热下载成果";
    export default {
        props: {
            id: {
                type: String,
                default: ""
            },
            count: {
                type: Number,
                default: 10
            }
        },
        data: function data() {
            return {
                data: {
                    "fields": fields,
                    "records": []
                },
                ischange: 0,
                hidden: false,
                title: "下载历史成果",
                style: "width:410px;"
            }
        },
        mounted: function() {
            this.getData();
        },
        methods: {
            //雷晶GetDownloadMessByUserId方法
            getData: function() {
                count++;
                let _this = this;
                let _count = this.count;
                let _type = "download";
                this.data.records = [];
                $.ajax({
                    url: "/User/GetUserBehaviorGttop",
                    type: "get",
                    //async: false,
                    data: {
                        count: _count,
                        type: _type
                    },
                    success: function(result) {
                        for (let i = 0; i < result.length; i++) {
                            if (!result[i]) return;
                            _this.data.records.push({
                                "id": result[i].iiid,
                                "name": result[i].DC_Title_Text,
                                "author": result[i].DC_Author,
                                "downloadcount": result[i].CountDownload
                            });
                        }
                        _this.ischange = count; //watch
                    }
                });
                
            },
            //点击td某一项打开详情页面
            tdClick: function(url) {
                // window.open("/GeoTopic/Detail?iiid=" + url);
                window.open(url);
            },
            //刷新按钮
            refresh: function() {
                this.getData();
            },
        },
        components: {
            top: soUI.top,
        }
    }
</script>


