<template>
    <div class="newEstPt">
        <top :data="data" :title="title"
        :style="style" :hidden="hidden" :class="" :ischange="ischange"
        :onrefresh="refresh" :ontdclick="tdClick">
    </top>
</div>
</template>
<script>
    import  soUI from '../../SoUI/soUI';
    import ajax from '../../Utils/pollAjax'
    // import _ajax from '../../Utils/coment'
    
    // var pram= {
    //     "filter": {},
    //     "fields": {},
    //     "sortrules": {
    //         "dc.date.value": {
    //             "direction": -1,
    //             "promotion": []
    //         }
    //     },
    //     "pager": {
    //         "from": 0,
    //         "size": 10
    //     }
    // };
    var count=1;
    var _param=
    {
        "sortrules": {
            "dc.date.value": {
                "direction": -1
            }
        },
        "pager": {
            "From": 0,
            "Size": 10
        }
    }
    _param = JSON.stringify(_param);
    var fields= [
    {
        "name":"name",
        "title":"名称",
        "type":"",
        "key":false,
        "visible":"",
        "url":"/Render/PTDetail?iiid=<#= id#>"
    },
    {
        "name":"author", 
        "title":"作者", 
        "type":"", 
        "key":false, 
        "visible":""
    },
    {
        "name":"createddate", 
        "title":"创建日期", 
        "type":"", 
        "key":false, 
        "visible":""
    }
    ];

    export default {
        props: {
            id:{
                type:String,
                default:null
            }
        },
        data: function data() {
            return {
                data:{
                    "fields":fields,
                    "records":[]
                },
                ischange:0,
                hidden:false,
                title:"最新入库成果",
                style:"width:410px;"
            }
        },
        mounted: function () {
            this.getData();
        },
        methods: {
            //石亚茹Match方法
            getData:function () {
                count++;
                let _this = this;
                this.data.records=[];
                $.ajax({
                    url:"/Document/GetMatchData",
                    type:"get",
                    //async:false,
                    data:{param: _param},
                    success:function(result){
                        //var _result=JSON.parse(result).metadatas;
                        var _result=JSON.parse(result).metadatas;   //这是重油项目的
                        for (let i = 0; i < _result.length; i++) {
                            if (!_result[i]) return;
                            _this.data.records.push({
                                "id": _result[i].iiid,
                                "name":_result[i].dc.title[0].text?_result[i].dc.title[0].text:'',
                                "author":_result[i].dc.contributor?_result[i].dc.contributor[0].name:'',    
                                "createddate":_result[i].indexeddate?_result[i].indexeddate.slice(0,10).replace(/-/g,'/'):""
                                //eval('new'+eval(_result[i].indexeddate).source).toLocaleDateString()
                            });
                        }
                        _this.ischange = count;   //watch
                    }
                });
                
           },
            //点击td某一项打开详情页面
            tdClick:function (url) {
               window.open(url);
            },
            //刷新按钮
            refresh:function () {
                this.data.records=[];
                this.getData();
            },
        },
        components:{
            top:soUI.top,
        }
    }
</script>


