          var pagefilter = { \"from\": 0, \"size\": this.size };\n            this.SetParms(pagefilter);\n            this.getData();\n        },\n        methods: {\n            //石亚茹Match接口\n            getData: function () {\n                let pmdata = this.param;\n                delete pmdata[\"viewtype\"];\n                let _this = this;\n                debugger;\n                $.ajax({\n                    url: \"/Document/GetMatchData\",\n                    data: { param: JSON.stringify(pmdata) },\n                    type: \"get\",\n                    cache: false,\n                    async: false,\n                    dataType: 'json',\n                    success: function (result) {\n                        let data = JSON.parse(result);\n                        _this.count = data.count;\n                        _this.GetCollection();\n                        _this.GetListData(data.metadatas);\n                        if (data.count > _this.size) _this.allowpaging = true;\n                    },\n                    error: function (err) {\n                        console.log(err);\n                    }\n                });\n            },\n\n            Pager: function (pageIndex) {\n                var startIndex = (pageIndex - 1) * this.size;\n                var pagefilter = { \"from\": startIndex, \"size\": this.size };\n                this.SetParms(pagefilter);\n                this.getData();\n            },\n\n            TitleClick: function (value) {\n                window.open(\"/Render/PTDetail?iiid=\" + value.id)\n            },\n\n            BindData: function (data) {\n                this.count = data.count;\n                //this.GetListData(data.metadatas);\n                //this.GetFilterData(data.groups);\n            },\n\n            collectClick: function (item) {\n                let data = {\n                    \"BehaviorType\": \"favorite\",\n                    \"Type\": \"list\",\n                    \"Title\": item.title,\n                    \"Content\": JSON.stringify({ id: item.id }),\n                };\n                if (item.collectImg == \"save\") {\n                    $.ajax({\n                        url: \"/UserBehavior/AddFavoriteMess\",\n                        data: { data: JSON.stringify(data) },\n                        type: \"get\",\n                        async: false,\n                        cache: false,\n                        dataType: 'json',\n                        success: function (result) {\n                            gtUI.message({ message: \"收藏成功！\", type: 'success' });\n                        },\n                        error: function (result) {\n                            gtUI.message({ message: \"收藏失败！\", type: 'warning' });\n                            console.log(result);\n                        }\n                    });\n                }\n                else gtUI.message({ message: \"已收藏！\", type: 'info' });\n            },\n\n            FilterClick: function () { },\n            FilterClean: function () { },\n\n            GetCollection() {\n                let _this = this;\n                $.ajax({\n                    url: \"/UserBehavior/GetAllFavoriteMessByUserId\",\n                    data: {},\n                    type: \"get\",\n                    cache: false,\n                    async: false,\n                    dataType: 'json',\n                    success: function (result) {\n                        _this.collections = result;\n                    },\n                    error: function (result) {\n                        console.log(result);\n                    }\n                });\n            },\n\n            GetCollectImg: function (id) {\n                for (let i in this.collections) {\n                    let content = { id: \"\" };\n                    try {\n                        content = JSON.parse(this.collections[i][\"Content\"]);\n                        if (content.id == id) return \"collect\";\n                    }\n                    catch (err) {\n                    }\n                }\n                return \"save\";\n            },\n\n            GetListData: function (data) {\n                this.datalist = [];\n                for (var i = 0; i < data.length; i++) {\n                    listModel = {\n                        id: \"\",\n                        source: \"\",\n                        title: \"\",\n                        collectImg: \"\",\n                        format: \"\",\n                        createDate: \"\",\n                        author: \"\",\n                        summary: \"\",\n                        imgStr: \"\",\n                    };\n                    var item = data[i];\n                    listModel.id = item.iiid || \"\";\n                    listModel.source = item[\"source\"][\"name\"] || \"\";\n\n                    //title有多个\n                    listModel.title = item[\"dc\"][\"title\"][0][\"text\"] || \"\";\n                    listModel.collectImg = this.GetCollectImg(listModel.id);\n                    //format找不到对应字段\n                    listModel.format = item[\"source\"][\"format\"] || \"\";\n                    if (listModel.format != \"\")\n                        listModel.format = listModel.format.toLocaleLowerCase();\n\n                    let dateStr = item[\"indexeddate\"] || item[\"dc\"][\"date\"][0][\"value\"] || \"\"\n                    listModel.createDate = dateStr.substr(0, dateStr.indexOf('T'));\n\n                    let authors = item[\"dc\"][\"contributor\"] || [];\n                    for (let j = 0; j < authors.length; j++) {\n                        if (authors[j][\"type\"] == \"Author\") {\n                            listModel.author = authors[j][\"name\"] || \"\";\n                            break;\n                        }\n                    }\n\n                    let descrips = item[\"dc\"][\"description\"] || [];\n                    for (